.BuildControls {
  background: #200122;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #6f0000, #200122);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#6f0000), to(#200122));
  background: -webkit-linear-gradient(left, #6f0000, #200122);
  background: -o-linear-gradient(left, #6f0000, #200122);
  background: linear-gradient(to right, #6f0000, #200122); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  padding: 15px 0px;
}

.price {
  width: 150px;
  margin: 0 auto;
  background: #F09819; 
  background: -webkit-linear-gradient(to right, #EDDE5D, #F09819);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#EDDE5D), to(#F09819));
  background: -webkit-linear-gradient(left, #EDDE5D, #F09819);
  background: -o-linear-gradient(left, #EDDE5D, #F09819);
  background: linear-gradient(to right, #EDDE5D, #F09819); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin-bottom: 13px;
  padding: 5px 10px;
  color: #DA22FF; 
  font-weight: bold;
}

.OrderButton {
  background-color: #DAD735;
  outline: none;
  cursor: pointer;
  border: 1px solid #966909;
  color: #966909;
  font-family: inherit;
  font-size: 1.2em;
  padding: 15px 30px;
  -webkit-box-shadow: 2px 2px 2px #966909;
          box-shadow: 2px 2px 2px #966909;
  text-transform: uppercase;
}

.OrderButton:hover, .OrderButton:active {
  background-color: #A0DB41;
  border: 1px solid #966909;
  color: #966909;
}

.OrderButton:disabled {
  background-color: #C7C6C6;
  cursor: not-allowed;
  border: 1px solid #ccc;
  color: #888888;
}

.OrderButton:not(:disabled) {
  -webkit-animation: enable 0.3s linear;
          animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}


