.NavigationItem {
  margin: 10px 0;
  width: 100%;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.NavigationItem a {
  color: #8F5C2C;
  text-decoration: none;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  outline: none;
}

.NavigationItem a:active,
.NavigationItem a:hover,
.NavigationItem a.active {
  color: #40A4C8;  
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    width: 129px;
    text-align: center;
  }
  
  .NavigationItem a {
    color: white;
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    outline: none;
  }
  
  .NavigationItem a:active,
  .NavigationItem a:hover,
  .NavigationItem a.active {
    background-color: #8F5C2C;
    border-bottom: 5px solid #40A4C8;
    color: white;  
  }
}



